button {
    background-color: green;
    color: white;
    border: none;
    width: 200px;
    height: 50px;
    border-radius: 50px;
    cursor: pointer;
    font-size: 16px;
    transition: all 0.3s ease;
  }
  
  button:hover {
    border: 2px solid green;
    background-color: transparent;
    color: green;
  }
  